
.hero {
    padding-top: 3rem;
    padding-bottom: 3rem;

     a {
        color: $fuchsia;
        transition: color ease-in-out 250ms, box-shadow ease-in-out 250ms;
        box-shadow: inset 0 0 0 0 $fuchsia;
        margin: 0 -0.25rem;
        padding: 0 0.25rem;

        &.active {
            color: $black;
            font-weight: 700;
        }
        
        &.strike {
            text-decoration: line-through;
            pointer-events: none;
        }

        &:hover {
            color: $white;
            box-shadow: inset 25rem 0 0 0 $fuchsia;
        }
    }


    p {
        
        padding-left: 56px;
        font-size: 1.75rem;
        font-weight: 400;
        line-height: 125%;
        padding-bottom: 1rem;
        @media (max-width: 768px) {
           font-size: 1rem;
           padding-left: 0;
           font-size: 1rem;
           line-height: 150%;
        }


        &:first-child {
            padding-bottom: 1rem;
            padding-left: 0;
            padding-top: 0;
            font-size: 3.75rem;
            font-weight: 400;
            display: flex;
            gap: 0.5rem;
            @media (max-width: 768px) {
               font-size: 1.5rem;
            }
        }

        &:nth-child(3) {
            font-size: 1.5rem;
            font-weight: 400;
            line-height: 125%;
            @media (max-width: 768px) {
               font-size: 1rem;
                padding-left: 0;
                font-size: 1rem;
                line-height: 150%;
            }
        }

        &:nth-child(4) {
            font-size: 1.25rem;
            font-weight: 400;
            line-height: 125%;
            @media (max-width: 768px) {
                font-size: 1rem;
                padding-left: 0;
                font-size: 1rem;
                line-height: 150%;
            }
        }

        svg {
            fill: #E3C942;
            transform: scaleX(-1);
            width:  3rem;
            @media (max-width: 768px) {
                width: 1.5rem;
            }
        }
    }

    .container--small {
        max-width: 768px;
    }
}