.masthead {
    padding-top: 1rem;
    padding-bottom: 1rem;
    width: 100%;
    
    .container {
        display: flex;
        justify-content: flex-end;
    }
    
    a {
        font-size: 1rem;
        color: $blackLight;
        transition: color ease-in-out 250ms, box-shadow ease-in-out 250ms;
        box-shadow: inset 0 0 0 0 $fuchsia;
        margin: 0 -0.25rem;
        padding: 0 0.25rem;

        @media (max-width: 768px){
            font-size: 0.875rem;
        }

        &.active {
            color: $black;
            font-weight: 700;
        }
        
        &.strike {
            text-decoration: line-through;
            pointer-events: none;
        }

        &:hover {
            color: $white;
            box-shadow: inset 25rem 0 0 0 $fuchsia;
        }
    }
    
    nav {
        display: flex;
        gap: 1rem;
    }
    
    @media (max-width: 568px){
        .container {
            flex-direction: column;
            justify-content: space-between;
            gap: 1rem;
        }
        
        &.hide {
            top: -154px;
        }
    }
}
